import React from 'react'
import {TabBar,Button} from 'antd-mobile'

const Info = (props) => {
    const {pathname} = props.location;
    return (
        <div>
            <img 
            style={{width:'100%'}} 
            src={props.location.pathname.slice(6)} 
            alt=""      
            />
            <div style={{position: 'fixed', top: 0,width:'80px',height:'80px'}}>
                <TabBar tintColor="#33A3F4"
                    barTintColor="rgba(255,255,255,0)">
                <TabBar.Item 
                    icon={<i style={{fontSize:35}} className='iconfont icon-fanhui'></i>}
                    key="fanhui"
                    selected={pathname === '/home'}
                    onPress={() => {
                            props.history.push('/home')
                    }}>
                </TabBar.Item>
                </TabBar>
            </div>
            
            <div style={{ position: 'fixed', width: '100%',bottom: 0 ,color:'white'}}>
                <div style={{ position: 'fixed', width: '35%',bottom: 0 }}>
                    <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white"
                    >	
                        <TabBar.Item
                        icon={<i className='iconfont icon-dianpu'></i>}
                        title="店铺"
                        key="dianpu"
                        data-seed="logId1"
                        >
                        </TabBar.Item>
                        <TabBar.Item
                        icon={<i className='iconfont icon-kefu'></i>}
                        title="客服"
                        key="kefu"
                        data-seed="logId1"
                        >
                        </TabBar.Item>
                        <TabBar.Item
                        icon={<i className='iconfont icon-shoucang'></i>}
                        title="收藏"
                        key="shoucang"
                        data-seed="logId1"
                        >
                        </TabBar.Item>
                    </TabBar>
                </div> 
                <div style={{position: 'fixed', width: '65%', bottom: 0 ,right: 0 }}>
                    <Button 
                        style={{float:'right',width:'50%',
                        backgroundColor:'#ff7401',color:'white',
                        fontSize:'16px',fontWeight:'bold',
                        borderRadius:'0 50PX 50PX 0'}}>
                            立即购买</Button>
                    <Button 
                        style={{backgroundColor:'#ffbf00',color:'white',
                        fontSize:'16px',fontWeight:'bold',
                        borderRadius:'50px 0 0 50px'}}>
                            加入购物车</Button>
                </div>
            </div>
        </div>
    )
}

export default Info;
